import { AButton } from '@/shared/ui/Button/AAButton';
import EventCardAction from '@/shared/ui/Cards/EventEntityCard/CardAction';
import EVCard from '@/shared/ui/Cards/EventEntityCard/EventCard';
import { SendCard, SendCardActionProp } from '@/shared/ui/Cards/SendCard/SendCard';
import ASelect from '@/shared/ui/Inputs/Select/ASelect';
import React, { FC } from 'react';

interface NewTaskInitQuestionProps {
    action: () => void
}

export const NewTaskInitQuestion: FC<NewTaskInitQuestionProps> = ({ action }) => {
    const send = {
        color: 'blue',
        handler: action,
        title: 'НАЧАТЬ ПРОДАВАТЬ',
        isActive: true
    } as SendCardActionProp

    const cancel = {
        color: 'grey',
        handler: action,
        title: 'НЕ НАЧИНАТЬ',
        isActive: false
    } as SendCardActionProp

    return <SendCard
        title='Создание задачи'
        send={send}
        cancel={cancel}
        content={
            <div style={{ 'height': '300px' }}>
                <h1>У вас нет задач по данной компании</h1>
            </div>
        }
    />
}

export default NewTaskInitQuestion;