import { EV_REPORT_PROP } from "@/entities/EventReport/type/event-report-type";
import { FC } from "react";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'

interface ATextProps {
    label: string | null,
    height: number,
    nameForHandler: EV_REPORT_PROP.COMMENT,
    handleChange: (
        value: string
    ) => void,

}


const AText: FC<ATextProps> = ({
    label,
    height,
    nameForHandler,
    handleChange,

}) => {
    const id = `input-${nameForHandler}`;
    return (
        
            <div className={`${style.inputWrapper}`}>
                {label && <ALabel
                    htmlId={id}
                    label={label}
                    errorMessage={null}

                />}
                <textarea
                    onChange={
                        (e) => handleChange(
                            e.target.value
                        )}
                    // onFocus={handleOnNameFocus}
                    className="form-control"
                    id={id}
                    lang="rus"
                    rows={height}
                />
            </div>

        
    );
}

export default AText;