import React, { FC, useState } from 'react';
import * as classes from '../../Report.module.scss'
import { EV_REPORT_PROP, EventReportStateReport } from '@/entities/EventReport/type/event-report-type';
import { Col, Container, Row } from 'reactstrap';
import ASelect from '@/shared/ui/Inputs/Select/ASelect';
import EVCard from '../../../../../shared/ui/Cards/EventEntityCard/EventCard';
import EventCardAction from '@/shared/ui/Cards/EventEntityCard/CardAction';
import { Department } from '@/features/Departament';
import { DEPARTAMENT_STATE_PROP } from '@/features/Departament/type/department-type';

interface ResultProps {
    report: EventReportStateReport
    handleChange: (type: EV_REPORT_PROP, value: string) => void
}
const Result: FC<ResultProps> = ({
    report,
    handleChange
}) => {

    const [isDepartmentShow, setIsDepartmentShow] = useState(false)
    const [planActionName, setPlanActionName] = useState('Сотрудник')

    const planModeHendler = () => {
        let actionName = 'Сотрудник'
        if (!isDepartmentShow) {
            actionName = 'Вернуться'
        }
        setPlanActionName(actionName)
        setIsDepartmentShow(!isDepartmentShow)
    }


    return <EVCard title={'Результат'} width={12}
        // actionComponent={
        //     <EventCardAction
        //         title={planActionName}
        //         hendler={planModeHendler}
        //     />
        // }
    >
        {isDepartmentShow ?
            <Department
                from={DEPARTAMENT_STATE_PROP.PLAN}
            />
            : <>
                {/* <Col className={classes.status} sm={6}> */}

                <ASelect
                    label={'Статус Работы'}
                    nameForHandler={EV_REPORT_PROP.WORK_STATUS}
                    handleChange={handleChange}
                    current={report[EV_REPORT_PROP.WORK_STATUS].current}
                    items={report[EV_REPORT_PROP.WORK_STATUS].items}
                />




                {/* </Col> */}
                {/* <Col className={`${classes.status} ms-1`} sm={6}> */}
                {report[EV_REPORT_PROP.FAIL_TYPE].isActive && !report[EV_REPORT_PROP.FAIL_REASON].isActive &&
                    <ASelect
                        label={'Тип отказа'}
                        nameForHandler={EV_REPORT_PROP.FAIL_TYPE}
                        handleChange={handleChange}
                        current={report[EV_REPORT_PROP.FAIL_TYPE].current}
                        items={report[EV_REPORT_PROP.FAIL_TYPE].items}
                    />}
                {report[EV_REPORT_PROP.FAIL_REASON].isActive && <Col className={`${classes.itemsWrapper}`} sm={6}>

                    {/* <Col sm={6} className='p-0'> */}
                        <ASelect
                            label={'Тип отказа'}
                            nameForHandler={EV_REPORT_PROP.FAIL_TYPE}
                            handleChange={handleChange}
                            current={report[EV_REPORT_PROP.FAIL_TYPE].current}
                            items={report[EV_REPORT_PROP.FAIL_TYPE].items}
                        />
                    {/* </Col> */}
                    {/* <Col sm={6} className='p-0'> */}
                        <div className={`col-md-12 ${classes.lastSelectWrapper}`}>
                            <div className={`${classes.lastSelectInnerWrapper} p-0`}>
                                <ASelect
                                    label={'Причины отказа'}
                                    nameForHandler={EV_REPORT_PROP.FAIL_REASON}
                                    handleChange={handleChange}
                                    current={report[EV_REPORT_PROP.FAIL_REASON].current}
                                    items={report[EV_REPORT_PROP.FAIL_REASON].items}
                                />
                            </div>
                        </div>

                    {/* </Col> */}
                </Col>}
            </>}
    </EVCard>
}

export default Result;