import { AppDispatch, AppGetState } from "@/app/model/store"
import { bitrixAPI } from "@/app/services/bitrix-general-api"
import { PBX_GROUP, Portal } from "@/app/types/portal/portal-type"
import { eventTaskActions } from "@/entities/EventTask"
import { EventItemResultType, eventItemActions } from "./EventItemSlice"
import { EventTask } from "@/entities/EventTask/types/event-task-type"
import { eventActions } from "@/processes/event"
import { EVENT_CURRENT_PAGE } from "@/processes/event/types/event-types"
import { eventPresentationActions } from "@/entities/EventPresentation"
import { PresentationProp } from "@/entities/EventPresentation/model/PresSlice"
import { eventSaleActions } from "@/entities/EventSale"
import { getSalePresentationsList } from "@/entities/EventSale/model/EventSaleThunk"




export const getResultMenu = (
    type: EventItemResultType, task: EventTask | null
) => async (
    dispatch: AppDispatch, getState: AppGetState
) => {



        dispatch(eventTaskActions.setCurrentTask({
            task
        }))

        dispatch(eventItemActions.setEventItemMenuStatus({
            status: true,
            menuType: type
        }))

        dispatch(
            eventActions
                .setCurrentPage(
                    {
                        page: EVENT_CURRENT_PAGE.ITEM
                    }

                )
        )

        //если текущее событие презентация и оно результативное
        if (task) {
            const isCrrentReportEventPres = task.eventType === 'presentation'

            if (isCrrentReportEventPres && type === EventItemResultType.RESULT) {
                dispatch(
                    eventPresentationActions
                        .setPresentationProp(
                            {
                                name: PresentationProp.IS_PRESENTATION_DONE,
                                value: true
                            }
                        )
                )



            }
        }
    }

export const cancelResultMenu = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {

    dispatch(
        eventSaleActions
            .setCurrentPresList({
                taskId: null
            })

    )


    dispatch(eventTaskActions.setCurrentTask({ task: null }))
    dispatch(eventItemActions.setEventItemMenuStatus({
        status: false,
        menuType: null
    }))
    dispatch(
        eventActions
            .setCurrentPage(
                {
                    page: EVENT_CURRENT_PAGE.LIST
                }

            )
    )
}
