import { EV_PLAN_PROP } from "@/entities/EventPlan/type/event-plan-type";
import { EV_REPORT_PROP, EventReportSelectItem } from "@/entities/EventReport/type/event-report-type";
import { FC } from "react";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'
import { DUSER_ROLE } from "@/features/Departament/type/department-type";
import { BXUser } from "@/app/types/bitrix/bitrix-type";
import { ComplectSettingsSelectItem, GLOBAL_STATE_PROP } from "@/pages/document/ComplectInitialSettings/model/ComplectSettingsSlice";


interface ASelectProps {
    label: string,
    nameForHandler: EV_REPORT_PROP | EV_PLAN_PROP | DUSER_ROLE | GLOBAL_STATE_PROP,
    handleChange: (
        type: EV_REPORT_PROP | EV_PLAN_PROP | DUSER_ROLE,
        value: string | number

    ) => void,
    current: EventReportSelectItem | BXUser,
    items: Array<EventReportSelectItem> | Array<BXUser>
}


const ASelect: FC<ASelectProps> = ({
    label,
    nameForHandler,
    handleChange,
    current,
    items

}) => {
    const id = `input-${label}-select-${nameForHandler}`;


    const isEventReportSelectItem = (variable: any): variable is EventReportSelectItem => {
        return (variable as EventReportSelectItem).code !== undefined;
    };
    const isBXUser = (variable: any): variable is BXUser => {
        return (variable as BXUser).ID !== undefined;
    };
    const isComplectSetting = (variable: any): variable is ComplectSettingsSelectItem => {
        return (variable as ComplectSettingsSelectItem).id !== undefined;
    };
    const isEventReport = isEventReportSelectItem(current)
    
    let value = isEventReport ? current.code : current.ID;

    return (
        <>

            <div className={`${style.inputWrapper} col-md-12`}>
                <ALabel
                    htmlId={id}
                    label={label}
                    errorMessage={null}

                />

                <select className={`${style.input} form-control `}
                    value={value}
                    //@ts-ignore
                    onChange={(e) => handleChange(nameForHandler, e.target.value)}
                >
                    {items.map(item => {
                        if (isEventReportSelectItem(item)) {
                            return (
                                <option key={`${nameForHandler}-${item.id}`} value={item.code || item.id}>{item.name}</option>
                            );
                        } else if (isBXUser(item)) {
                            return (

                                <option key={`${nameForHandler}-${item.ID}`} value={item.ID}>{item.NAME} {item.LAST_NAME}</option>
                            );
                        } else if (isComplectSetting(item)) {
                            const curentComplectSettingItem = item as ComplectSettingsSelectItem
                            return (

                                <option key={`${nameForHandler}-${curentComplectSettingItem.id}`} value={curentComplectSettingItem.id}>{curentComplectSettingItem.title}</option>
                            );
                        }
                    })}
                </select>
            </div>

        </>
    );
}

export default ASelect;